.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 50px;
}

.App-header {
  background-color: #001529;
  display: flex;
  align-items: center;
  color: white;
  height: 50px;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: -1;
}

.App-title {
  font-style: italic;
  font-size: 20px;
  font-weight: 600;
  color: #61dafb;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
